/* primary: #106BA3 */
:root{
    --aside-width: 300px;
}

* {
    font-family: "Segoe UI", sans-serif;
}

body {
    position: relative;
}

.db-navbar-grid-container {
    display: grid;
    grid-template-columns: auto;
}

.db-navbar-grid-item,
.db-dabing-grid-item  {
    padding-left: 1em;
    padding-right: 1em;
}

.db-dabing-grid-item {
    padding-bottom: 1em;
    min-height: 100vh;
}

.db-dabing-grid-container {
    display: grid;
    grid-template-columns: auto var(--aside-width);
}

.db-dabing-grid-item > * {
    margin-top: 1em;
}

.db-small-grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(125px, 1fr));
    gap: 1em;
    margin-bottom: 1em;
}

.db-medium-grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(175px, 1fr));
    gap: 1em;
    margin-bottom: 1em;
}

.db-large-grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1em;
    margin-bottom: 1em;
}

.db-huge-grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
    gap: 1em;
    margin-bottom: 1em;
}

.db-dashboard-card-container > *{
    margin-top: 1em;
}

#tii {
    grid-column: 1 / span 2;
}

@media (max-width: calc(4em + 300px + 2 * 250px)){
    #tii {
        grid-column: 1 / span 1;
    }
}

.db-medium .db-input-element {
    display: grid;
    grid-template-columns: 160px calc(30px + 1em) auto;
}

.db-alarm-config-element {
    display: grid;
    grid-template-columns: 75px calc(30px + 1em) auto;
}

.db-input-element .bp3-form-group-sub-label,
.db-input-element label.bp3-switch {
    margin: 0;
}

.db-input-element .bp3-form-content {
    margin: auto 0;
}

.db-widget-container {
    display: grid;
    gap: 1em;
}

.db-status-container {
    display: grid;
    grid-template-columns: auto max-content;
    gap: 0.5em;
}

.db-action-buttons {
    display: flex;
    gap: 1em;
}

.db-aside-panel {
    background-color: #106BA3;
}

.db-main-container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

.db-main-container.db-small {
    width: 350px;
}

.db-main-container.db-medium {
    width: 700px;
}

.db-main-container.db-large {
    width: 1050px;
}

.db-small {
    max-width: 350px;
}

.db-medium {
    max-width: 700px;
}

.db-large {
    max-width: 1050px;
}

.db-card {
    padding: 1em;
}

.db-primary-card {
    border: solid 1px #106BA3;
}

.db-small-card {
    width: 310px;
}

.bp3-control.bp3-switch input ~ .bp3-control-indicator {
    background-color: #db3838;
}

.bp3-control.bp3-switch:hover input ~ .bp3-control-indicator {
    background-color: #C23030;
}

.bp3-toast-container.bp3-toast-container-inline {
    position: fixed;
}

.db-overflow {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.db-service-label.db-overflow {
    max-width: calc(var(--aside-width) - 4em - 92px);
}

/* TABLE */
.db-table.db-th-right th {
    text-align: right;
    vertical-align: top;
}

.db-table.db-th-left th {
    text-align: left;
    vertical-align: top;
}

.db-table.db-th-left td {
    padding-left: 1em;
}

/* LOGO */
#logo {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin: 0;
}

#logo:hover > span {
    background-color: #106ba3;
    box-shadow: inset 0 0 0 1px rgba(16, 22, 26, 0.4), inset 0 -1px 0 rgba(16, 22, 26, 0.2);
}

#logo span {
    padding: 0.25em;
    margin: 0 0.25em;
    min-height: initial;
    min-width: initial;
    vertical-align: initial;
}